/* Apenas o que não dá para fazer com Tailwind */
.footerWave {
  position: absolute;
  top: -1px;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
}

.footerWave svg {
  position: relative;
  display: block;
  width: calc(100% + 1.3px);
  height: 40px;
}

.shapeFill {
  fill: #FFFFFF;
}

